import { FC } from 'react'
import * as S from './style'

type DomainItemProps = {
  domainName: string
  price?: number
  onClick: () => void
  withPrice?: boolean
  toggleBuyMode?: () => void
  isBuyMode?: boolean
  toggleQrModalVisible?: () => void
  paymentLink?: string
}

export const DomainItem: FC<DomainItemProps> = (props) => {
  const {
    domainName,
    price,
    onClick,
    withPrice,
    isBuyMode,
    toggleQrModalVisible,
    paymentLink,
  } = props

  return (
    <S.Wrapper isBuyMode={isBuyMode}>
      <S.Label isBuyMode={isBuyMode}>{domainName}</S.Label>
      {isBuyMode ? (
        <S.BtnsWrapper>
          <S.Button
            isBuyMode={isBuyMode}
            onClick={toggleQrModalVisible}
            variant="gray"
          >
            QR
          </S.Button>
          <S.Link href={paymentLink}>PAY</S.Link>
        </S.BtnsWrapper>
      ) : (
        <S.Button onClick={onClick}>
          {withPrice ? `${price} TON` : 'BUY'}
        </S.Button>
      )}
    </S.Wrapper>
  )
}
