import { HTMLInputTypeAttribute, ChangeEvent, FC } from 'react'
import { SelectProps } from 'ui/Select/Select'

import * as S from './style'

type InputProps = {
  className?: string
  isDisabled?: boolean
  isError?: boolean
  inputProps: {
    onChange: (evt: ChangeEvent<HTMLInputElement>) => void
    value?: string
    placeholder?: string
    name?: string
    type?: HTMLInputTypeAttribute
  }
  selectProps: SelectProps
  isLoading?: boolean
}

export const SearchInput: FC<InputProps> = (props) => {
  const { className, isDisabled, inputProps, isError, selectProps } = props

  return (
    <S.Wrapper>
      <S.InputContainer
        className={className}
        isDisabled={isDisabled}
        isError={isError}
      >
        <S.Input {...inputProps} />
        <S.Select {...selectProps} />
      </S.InputContainer>
      {isError && (
        <S.ErrorLabel>This domain name is already taken :(</S.ErrorLabel>
      )}
    </S.Wrapper>
  )
}
