import { useCallback, FC, useState } from 'react'
import { SvgLogo, SvgHamburgerIcon } from 'ui/icons'
import * as S from './style'

const navLinks = [
  {
    label: 'Web3Mining DNS',
    link: '/',
    isActive: true,
  },
  {
    label: 'Web3Mining NFT',
    link: 'https://tonscan.org/address/EQBUKxK-NPOlxmkfwjbOuoWpDxSt3bXjI1K-ZDYMRtkbXgOl',
  },
  {
    label: 'Web3Mining Community',
    link: 'https://t.me/web3mining',
  },
]

export const Header: FC = () => {
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState<boolean>(false)

  const toggleMobileMenu = useCallback(() => {
    setIsMobileMenuOpen((prev) => !prev)
  }, [])

  return (
    <S.Wrapper>
      <SvgLogo />
      <S.LinkWrapper>
        {navLinks.map(({ label, link, isActive }) =>
          isActive ? (
            <S.LinkItem key={label} as="div" isActive={isActive}>
              {label}
            </S.LinkItem>
          ) : (
            <S.LinkItem
              key={label}
              href={link}
              isActive={isActive}
              rel="noreferrer"
              target="_blank"
            >
              {label}
            </S.LinkItem>
          )
        )}
      </S.LinkWrapper>
      <S.Hamburger onClick={toggleMobileMenu}>
        <SvgHamburgerIcon />
      </S.Hamburger>
      {isMobileMenuOpen && (
        <S.StyledMobileMenu
          navLinks={navLinks}
          toggleMobileMenu={toggleMobileMenu}
        />
      )}
    </S.Wrapper>
  )
}
