import { useRef, useCallback, useEffect } from 'react'
import ReactDOM from 'react-dom'
import { FCWithChildren } from 'types/app'
import * as S from './style'

type ModalProps = {
  title?: string
  className?: string
  onClose: () => void
}

const PORTAL_TARGET = 'portal'

const portalElement = document.getElementById(PORTAL_TARGET) as HTMLElement

export const Modal: FCWithChildren<ModalProps> = (props) => {
  const { className, onClose, children, title } = props

  const outsideRef = useRef(null)

  const handleWrapperClick = (
    evt: React.MouseEvent<HTMLDivElement, MouseEvent>
  ) => {
    if (evt.target === outsideRef.current) {
      onClose()
    }
  }

  const handleKeyDown = useCallback(
    ({ key }: KeyboardEvent) => {
      if (key === 'Escape') {
        onClose()
      }
    },
    [onClose]
  )

  useEffect(() => {
    document.body.style.overflow = 'hidden'
    return () => {
      document.body.style.overflow = 'auto'
    }
  }, [])

  useEffect(() => {
    document.addEventListener('keydown', handleKeyDown)
    return () => document.removeEventListener('keydown', handleKeyDown)
  }, [handleKeyDown])

  return ReactDOM.createPortal(
    <S.WrapModal ref={outsideRef} onClick={handleWrapperClick}>
      <S.Wrapper className={className}>
        <S.Title>{title}</S.Title>
        {children}
        <S.Button onClick={onClose} variant="gray">
          Close
        </S.Button>
      </S.Wrapper>
    </S.WrapModal>,
    portalElement
  )
}
