import { FC } from 'react'
import * as S from './style'

type MobileMenuProps = {
  className?: string
  navLinks: {
    label: string
    link: string
    isActive?: boolean
  }[]
  toggleMobileMenu: () => void
}

export const MobileMenu: FC<MobileMenuProps> = (props) => {
  const { className, navLinks, toggleMobileMenu } = props

  return (
    <S.Wrapper className={className}>
      <S.LinkWrapper>
        {navLinks.map(({ label, link, isActive }) =>
          isActive ? (
            <S.LinkItem key={label} as="div" isActive={isActive}>
              {label}
            </S.LinkItem>
          ) : (
            <S.LinkItem
              key={label}
              href={link}
              isActive={isActive}
              onClick={toggleMobileMenu}
              rel="noreferrer"
              target="_blank"
            >
              {label}
            </S.LinkItem>
          )
        )}
      </S.LinkWrapper>
    </S.Wrapper>
  )
}
