import { useCallback, FC, useState } from 'react'
import { useQuery } from 'react-query'
import { SingleValue } from 'react-select'
import { getDomains, getResolvedDomains } from 'api'
import { Domain } from 'api/types'
import { useDebounce } from 'hooks'
import { QrModal } from 'modals/QrModal/QrModal'
import { DomainItem } from 'ui/DomainItem/DomainItem'
import { SearchInput } from 'ui/SearchInput/SearchInput'
import { Spinner } from 'ui/Spinner/Spinner'
import { getPaymentLink, getDomainPrice } from 'utils'
import * as S from './style'

export const Main: FC = () => {
  const [typedDomainName, setTypedDomainName] = useState<string>('')
  const [selectedDomain, setSelectedDomain] = useState<Domain>()
  const [isBuyMode, setIsBuyMode] = useState<boolean>(false)
  const [isQrModalVisible, setIsQrModalVisible] = useState<boolean>(false)
  const [isDomainExist, setIsDomainExist] = useState(false)

  const debouncedTypedDomainName = useDebounce(typedDomainName, 300)

  const { data: domainData, isSuccess: isDomainDataLoaded } = useQuery(
    ['domains'],
    () => getDomains(),
    {
      select: useCallback((data: { resolvers: Domain[] }) => {
        const formattedDomainsData = data.resolvers.map(
          ({ address, domain, prices }) => ({
            domain: '.' + domain,
            address,
            prices,
          })
        )

        setSelectedDomain(formattedDomainsData[0])

        return formattedDomainsData
      }, []),
    }
  )

  const { isFetching: isResolvedDomainsFetching } = useQuery(
    ['resolved-domains', debouncedTypedDomainName, selectedDomain],
    () => getResolvedDomains(debouncedTypedDomainName + selectedDomain?.domain),
    {
      enabled: debouncedTypedDomainName !== '' && selectedDomain !== undefined,
      onSuccess: (data) => {
        if (!data.wallet) {
          setIsDomainExist(false)

          return
        }

        setIsDomainExist(true)
      },
      onError: () => {
        setIsDomainExist(false)
      },
    }
  )

  const handleInputChange = useCallback(
    (value: string) => {
      if (!value && isDomainExist) {
        setIsDomainExist(false)
        setTypedDomainName(value)

        return
      }

      if (!value && isBuyMode) {
        setIsBuyMode(false)
        setTypedDomainName(value)

        return
      }

      if (value && isBuyMode) {
        setIsBuyMode(false)
        setTypedDomainName(value)
      }

      const engRegex = /^[a-zA-Z0-9-\s]*$/

      if (engRegex.test(value) && value.length <= 127) {
        setTypedDomainName(value.replace(/\s/g, '-'))

        return
      }
    },
    [isBuyMode, isDomainExist]
  )

  const handleSelectChange = useCallback(
    (domain: SingleValue<any>) => {
      if (isBuyMode) {
        setIsBuyMode(false)
      }

      setSelectedDomain(domain)
    },
    [isBuyMode]
  )

  const toggleBuyMode = useCallback(() => {
    setIsBuyMode((prev) => !prev)
  }, [])

  const toggleQrModalVisible = useCallback(() => {
    setIsQrModalVisible((prev) => !prev)
  }, [])

  const handleBuyBtnClick = useCallback(
    (domain: Domain) => {
      setSelectedDomain(domain)
      toggleBuyMode()
    },
    [toggleBuyMode]
  )

  if (isDomainDataLoaded && selectedDomain) {
    const getCurentDomainPrice = (prices: { [key: number]: number }) => {
      return getDomainPrice({
        domain: debouncedTypedDomainName,
        prices,
      })
    }

    const paymentLink = getPaymentLink({
      priceForDomain: getCurentDomainPrice(selectedDomain.prices),
      contractAddress: selectedDomain.address,
      typedDomainName,
    })

    return (
      <S.Wrapper>
        <S.TextBlock>
          <S.Title>Buy .ton subdomains</S.Title>
          <S.Description>
            Give crypto wallets, smart contracts or websites short readable
            names.
          </S.Description>
        </S.TextBlock>
        <SearchInput
          inputProps={{
            onChange: (evt) => {
              handleInputChange(evt.target.value.toLowerCase())
            },
            value: typedDomainName,
            placeholder: 'Enter subdomain',
          }}
          isError={isDomainExist}
          isLoading={isResolvedDomainsFetching}
          selectProps={{
            options: domainData,
            value: selectedDomain,
            onChange: handleSelectChange,
          }}
        />
        {isResolvedDomainsFetching ? (
          <Spinner />
        ) : (
          <>
            {!isDomainExist && debouncedTypedDomainName && (
              <S.InfoBlock>
                <S.Label>Information</S.Label>
                <DomainItem
                  domainName={typedDomainName + selectedDomain.domain}
                  isBuyMode={isBuyMode}
                  onClick={toggleBuyMode}
                  paymentLink={paymentLink}
                  price={getCurentDomainPrice(selectedDomain.prices)}
                  toggleBuyMode={toggleBuyMode}
                  toggleQrModalVisible={toggleQrModalVisible}
                  withPrice
                />
              </S.InfoBlock>
            )}

            {!isDomainExist && debouncedTypedDomainName && !isBuyMode && (
              <S.InfoBlock>
                <S.Label>Suggestions</S.Label>
                {domainData.map((domain) => {
                  const { domain: receivedDomain } = domain

                  return (
                    receivedDomain !== selectedDomain.domain && (
                      <DomainItem
                        key={receivedDomain}
                        domainName={typedDomainName + receivedDomain}
                        onClick={() => handleBuyBtnClick(domain)}
                        price={getCurentDomainPrice(domain.prices)}
                        withPrice
                      />
                    )
                  )
                })}
              </S.InfoBlock>
            )}
          </>
        )}
        {isQrModalVisible && (
          <QrModal onClose={toggleQrModalVisible} qrLink={paymentLink} />
        )}
      </S.Wrapper>
    )
  }

  return null
}
