import { FC } from 'react'
import QRCode from 'react-qr-code'
import * as S from './style'

const QR_SIZE = 277

const MODAL_TITLE = 'Scan QR-code to buy subdomain.'

type QrModalProps = {
  onClose: () => void
  qrLink: string
}

export const QrModal: FC<QrModalProps> = (props) => {
  const { onClose, qrLink } = props

  return (
    <S.Modal onClose={onClose} title={MODAL_TITLE}>
      <QRCode bgColor="none" fgColor="#fff" size={QR_SIZE} value={qrLink} />
    </S.Modal>
  )
}
