import { FCWithChildren } from 'types/app'
import * as S from './style'
import { VariantType } from './types'

type ButtonProps = {
  className?: string
  onClick?: () => void
  isDisabled?: boolean
  isLoading?: boolean
  variant?: VariantType
}

export const Button: FCWithChildren<ButtonProps> = (props) => {
  const { className, onClick, isDisabled, children, variant = 'purple' } = props

  return (
    <S.Wrapper
      className={className}
      disabled={isDisabled}
      onClick={onClick}
      variant={variant}
    >
      {children}
    </S.Wrapper>
  )
}
