import axios from 'axios'
import { Domain } from './types'

const DOMAINS_URL = 'https://raw.githubusercontent.com/web3mining/dns/main/domains.json'

const TON_API_DNS_RESOLVE_URL = 'https://tonapi.io/v1/dns/resolve'

const clientSideKey = process.env.REACT_APP_TON_API_KEY

export const getDomains = async () => {
  const { data } = await axios.get<{ resolvers: Domain[] }>(DOMAINS_URL)

  return data
}

export const getResolvedDomains = async (name: string) => {
  const { data } = await axios.get(TON_API_DNS_RESOLVE_URL, {
    headers: {
      Authorization: 'Bearer ' + clientSideKey,
    },
    params: {
      name,
    },
  })

  return data
}
