import TonWeb from 'tonweb'

type Args = {
  priceForDomain: number
  contractAddress: string
  typedDomainName: string
}

export const getPaymentLink = (args: Args): string => {
  const { priceForDomain, contractAddress, typedDomainName } = args

  if (priceForDomain > 0) {
    return `ton://transfer/${contractAddress}?amount=${TonWeb.utils.toNano(
      priceForDomain.toFixed(3)
    )}&text=${typedDomainName}`
  }

  return ''
}
