import { FC, useState } from 'react'
import { Domain } from 'api/types'
import { SvgArrow } from 'ui/icons'
import { Dropdown } from './components'
import * as S from './style'

export type SelectProps = {
  className?: string
  options: Domain[]
  value: Domain
  onChange: (value: Domain) => void
}

export const Select: FC<SelectProps> = (props) => {
  const { className, options, value, onChange } = props

  const [isDropdownShowed, setIsDropdownShowed] = useState<boolean>(false)

  const toggleDropdownShowed = () => {
    setIsDropdownShowed((prev) => !prev)
  }

  return (
    <S.Wrapper className={className} onClick={toggleDropdownShowed}>
      <S.Label>{value.domain}</S.Label>
      <SvgArrow />
      {isDropdownShowed && <Dropdown onChange={onChange} options={options} />}
    </S.Wrapper>
  )
}
