import { FC } from 'react'
import { Domain } from 'api/types'
import * as S from './style'

type DropdownProps = {
  options: Domain[]
  onChange: (value: Domain) => void
}

export const Dropdown: FC<DropdownProps> = (props) => {
  const { options, onChange } = props

  return (
    <S.Wrapper className="selectDropdown">
      {options.map((option) => (
        <S.DropdownItem key={option.domain} onClick={() => onChange(option)}>
          <S.Label>{option.domain}</S.Label>
        </S.DropdownItem>
      ))}
    </S.Wrapper>
  )
}
